

// GMC4 state
export const enum States {
	preWorking,
	programUndone,
	programFinished,
	operandNotEnough, // 
	InfinityLoopFound, // ()
}

// 
export const enum Ops {
	KeyToARegister = 0x0, // 0x0: KA: Aflag
	SetSevenSegment = 0x1, // 0x1: AO: 23ArLED
	ChangeABYZ = 0x2, // 0x2: CH:
	ChangeAY = 0x3, // 0x3: CY:
	ARegisterToYReferenceMemory = 0x4, // 0x4: AM
	YReferenceMemoryToARegister, // 0x5: MA
	MemoryPlus, // 0x6: M+
	MemoryMinus, // 0x7: M-
	StoreDirectNumberToARegister, // 0x8: TIA x
	AddARegisterToDirectNumber, // 0x9: AIA
	StoreDirectNumberToYRegister, // 0xA: TIY
	AddYRegisterToDirectNumber, // 0xB: AIY
	CompareDirectNumberToARegister, // 0xC: CIA
	CompareDirectNumberToYRegister, // 0xD: CIY
	CallService, // 0xE: CAL: 1Calls
	Jump, // 0xF: JUMP: 24bitx28bit(0x0-0x4F)
}

// (E0-Ef  CAL XXXX)
// - OpcodeKeyToARegister
export const enum Calls {
	SevenSegmentToOff = 0x0, // 0x0: RSTO: 7
	SetBinaryLeds, // 0x1: SETR: : 12LED[Y]
	ResetBinaryLeds, // 0x2: RSTR: : 02LED[Y]
	// 0x3FXK1,K2
	ReverseAllBitForARegister, // 0x4: CMPL: NOT(Ar)Ar
	SwapRegisterSet, // 0x5: CHNG: : A,B,Y,ZA',B',Y',Z'
	RightShiftForARegister, // 0x6: SIFT: Ar%2Flag,Ar/2Ar : Ar[0]
	BeepEnd, // 0x7: ENDS: : 
	BeepError, // 0x8: ERRS: : 
	BeepShort, // 0x9: SHTS: : 
	BeepLong, // 0xA: LONS: : 
	PlaySound, // 0xB: SUNS: : Ar
	Timer = 0xC, // 0xC: TIMR: : (Ar+1)x0.1sec
	DisplayBinaryLed, // 0xD: DSPR: : (E)2LED[0:3],(F)2LED[4:6]
	DEMminus, // 0xE: DEM-: DEC((Y)-Ar)(Y),Y-- : 
	DEMplus,  // 0xF: DEM+: DEC((Y)+Ar)(Y),Y-- : 

	KeyToARegister, // Opcode 0x0: KA: Aflag
	SetSevenSegment, // Opcode 0x1: AO: Ar7
}
