import Utils from '../gmc4/Utils';

/*

# WebAudio API

- GMC44WebAudio API

- 

- 
	- [Qiita: Web Audio API](https://qiita.com/Hibikine_Kage/items/8f55bf409883301a7a32)

	- [iOS](https://qiita.com/pentamania/items/2c568a9ec52148bbfd08)
		- safariRunContext

*/

// enum
// enum#sC#=Cs
export enum Tone { C , Cs, D, Ds, E, F, Fs, G, Gs, A, As, B}

export default class Beep {

	private CompatAudioContext = (window as any).AudioContext || (window as any).webkitAudioContext;
	private audioContext?: AudioContext;

	public async PlayTEST() {
		await this.PlayTone(Tone.A, 4, 0.3, 0.2, 150, 'sawtooth');
	}

	// 0x7: BeepEnd: 
	public async PlayEnd() {
		const oct = 3;
		const vol = 0.3;
		const atk = 0.2;
		const len = 60;
		const type: OscillatorType = 'sawtooth';
		await this.PlayTone(Tone.A, oct * 1, vol, atk, len, type);
		await this.PlayTone(Tone.B, oct * 1, vol, atk, len, type);
		await this.PlayTone(Tone.C, oct * 2, vol, atk, len, type);
		await this.PlayTone(Tone.D, oct * 2, vol, atk, len, type);
		await this.PlayTone(Tone.E, oct * 2, vol, atk, len, type);
		await this.PlayTone(Tone.F, oct * 2, vol, atk, len, type);
		await this.PlayTone(Tone.G, oct * 2, vol, atk, len, type);
	}


	// 0x8: BeepError: 
	public async PlayError() {
		const oct = 3;
		const vol = 0.2;
		const atk = 0.1;
		const len = 60;
		const type: OscillatorType = 'sine';
		for (let i = 0; i < 6; i++) {
			await this.PlayTone(Tone.B, oct * 0.5  , vol, atk, len, type);
			await this.PlayTone(Tone.B, oct * 0.25 , vol, atk, len / 2, type);
			await Utils.Sleep(len / 2);
		}
	}

	// 0x9: BeepShort:  
	public async PlayShort() {
		await this.PlayTone(Tone.A, 4, 0.3, 0.2, 20, 'sawtooth');
	}

	// 0xA: BeepLong:  
	public async PlayLong() {
		await this.PlayTone(Tone.A, 4, 0.3, 0.2, 20, 'sawtooth');
	}

	// 0xB: PlaySound: GMC4
	// - note: PDF(No67)0F
	public async PlaySound(index: number) {
		switch (index) {
			case 0x1:
				await this.PlayTone(Tone.A, 3, 0.3, 0.2, 20, 'sine');
				break;

			case 0x2:
				await this.PlayTone(Tone.B, 3, 0.3, 0.2, 20, 'sine');
				break;

			case 0x3:
				await this.PlayTone(Tone.C, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x4:
				await this.PlayTone(Tone.D, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x5:
				await this.PlayTone(Tone.E, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x6:
				await this.PlayTone(Tone.F, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x7:
				await this.PlayTone(Tone.G, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x8:
				await this.PlayTone(Tone.A, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0x9:
				await this.PlayTone(Tone.B, 4, 0.3, 0.2, 20, 'sine');
				break;

			case 0xA:
				await this.PlayTone(Tone.C, 5, 0.3, 0.2, 20, 'sine');
				break;

			case 0xB:
				await this.PlayTone(Tone.D, 5, 0.3, 0.2, 20, 'sine');
				break;

			case 0xC:
				await this.PlayTone(Tone.E, 5, 0.3, 0.2, 20, 'sine');
				break;

			case 0xD:
				await this.PlayTone(Tone.F, 5, 0.3, 0.2, 20, 'sine');
				break;

			case 0xE:
				await this.PlayTone(Tone.G, 5, 0.3, 0.2, 20, 'sine');
				break;
		}
	}

	// 
	private async PlayTone(
		tone: Tone,
		octave: number,
		volume: number,
		attack: number,
		length: number, // msec
		type: OscillatorType, // 'sine' | 'triangle' | 'square' | 'sawtooth'
	) {

		if (this.audioContext == null) {
			this.audioContext = new this.CompatAudioContext();
			if (this.audioContext == null) {
				console.log(`error: AudioContextnull`);
				return;
			}
		}

		const gainNode = this.audioContext.createGain();
		gainNode.connect(this.audioContext.destination);
		gainNode.gain.value = volume;
		gainNode.gain.linearRampToValueAtTime(volume, this.audioContext.currentTime + attack);

		const osc = this.audioContext.createOscillator();
		osc.connect(gainNode);
		osc.frequency.value = this.GetHz(tone, octave);
		osc.type = type;
		osc.start();
		await Utils.Sleep(length);
		osc.stop();
	}

	// 
	// index 0
	// C=0, C#=1, D=2, D#=3, E=4, F=5, F#=6, G=7, G#=8, A=9, A#=10, B=11
	// octave12()octave=0.51
	private GetHz(index: number, octave: number, base: number = 442): number {
		return base * octave * Math.pow(2, (1 / 12) * (index - 9));
	}
}
