export class DumpFormat {
	public registers: RegisterSet;
	public registers2: RegisterSet;
	public states: StateSet;
	public program: Uint8Array;
	public memory: Uint8Array;
	constructor(
		registerSet1: RegisterSet,
		registerSet2: RegisterSet,
		stateSet: StateSet,
		program: Uint8Array,
		memory: Uint8Array,
	) {
		this.registers = registerSet1;
		this.registers2 = registerSet2;
		this.states = stateSet;
		this.program = program;
		this.memory = memory;
	}
}

export class RegisterSet {
	public a: number;
	public b: number;
	public y: number;
	public z: number;

	constructor(a: number, b: number, y: number, z: number) {
		this.a = a;
		this.b = b;
		this.y = y;
		this.z = z;
	}

	public Reset(): void {
		this.a = 0;
		this.b = 0;
		this.y = 0;
		this.z = 0;
	}
}

export class StateSet {
	public step: number; // 
	public flag: boolean;
	public stepMode: boolean; // RUN: 
	public silentMode: boolean; // RUN: 

	constructor(
		step: number,
		flag: boolean,
		stepMode: boolean = false,
		silentMode: boolean = false,
	) {
		this.step = step;
		this.flag = flag;
		this.stepMode = stepMode;
		this.silentMode = silentMode;
	}

	public Reset(): void {
		this.step = 0;
	}

	public HardReset(): void {
		this.step = 0;
		this.flag = false;
		this.stepMode = false;
		this.silentMode = false;
	}
}
